import React, { Component } from 'react'
import "../Styles/style.css"

export default class Header extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             openedWindow: ["Home", "Info", "Contacts"],
        }
    }
    



    render() {
        return (
            <header id='header'>
                <button onClick={() => {this.props.navbarButtons(this.state.openedWindow[0])}}>Home page</button>
                <button onClick={() => {this.props.navbarButtons(this.state.openedWindow[1])}}>Info</button>
                <button onClick={() => {this.props.navbarButtons(this.state.openedWindow[2])}}>Contacts</button>
            </header>
        )
    }
}
